import common from '../../common/common.js'

export default {
	data() {
		return {
			contentList:[],
			info:{
				title:'合作加盟'
			},
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
		};
	},
	onLoad() {
		var that = this
		that.getJoinInfo();
		
		var currentPages = getCurrentPages()
		that.info.delta = currentPages.length
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
	},
	
	onPullDownRefresh:function(){
	  var that = this
	  uni.showNavigationBarLoading();
	  console.log('下拉刷新中...');		  
	  uni.hideNavigationBarLoading();
	  uni.stopPullDownRefresh();
	},
	
	methods: {
		//获取合作加盟
		getJoinInfo(){
			var that = this
			uni.request({
				url: common.apiUrl+'user/getJoinInfo',
				method: 'GET',
				data: {},
				success: res => {
					that.contentList = res.data.content_list;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//链接
		jumpUrl(e){
			var that = this
			var url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url:url
			})
		},
	}
}
